import React from 'react';
import cloudly from './wi-cloud.svg';
import sunny from './wi-sun.svg';
import rainy from './wi-rain.svg';
import {day} from './functions.js';
import {day1} from './functions.js';
import {day2} from './functions.js';
import {day3} from './functions.js';
import {dayNames} from './functions.js';
import {now} from './functions.js';
import {textout} from './functions.js';
let class_item ={
'Item':'item_Item__B-O8p',
'Sub':'item_Sub__10sLJ'
}

export const Item = () => {
	
	return (
	<div className={class_item.Item}> 
		<h7>Сегодня</h7>
		<br></br>
		<h3>{day}</h3>
		<br></br>
		<img src ={cloudly}/>
		<br></br>
		<h4> днем +26<sup>o</sup></h4>
		<h5> ночью +19<sup>o</sup></h5>
		<div className={class_item.Sub}>
			<center><h6>облачно,</h6></center>
			<center><h6>без осадков</h6></center>
		</div>
	</div>	
	);
}
export const Item_1 = () => {
	
	return (
	<div className={class_item.Item}> 
		<span>{dayNames[now.getDay()+1]}</span>
		<br></br>
		<h3>{day1}</h3>
		<br></br>
		<img src ={sunny}/>
		<br></br>
		<h4> днем +28<sup>o</sup></h4>
		<h5> ночью +21<sup>o</sup></h5>
		<div className={class_item.Sub}>
			<center><h6>солнечно,</h6></center>
			<center><h6>без осадков</h6></center>
		</div>
	</div>	
	);
}
